import React, {useState} from 'react';
import {useHistory} from 'react-router-dom';
import {Avatar, Button, Paper, Grid , Typography, Container} from '@material-ui/core';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import usestyle from './Style';
import axios from '../Reducer_axios/axios';
import {GoogleLogin} from 'react-google-login';
import { CustomInput } from './CustomInput';
import {ReactComponent as Icon} from '../img/googleIcon.svg';
import { useStateValue } from '../Reducer_axios/StateProvider';
import Header from '../Nav/Header';

const initials = {firstName:'', lastName:'', confirmPassword:'' , email:'', password:''} 

function Sign() {

    const history = useHistory();
    const classes = usestyle();

    const [dispatch] = useStateValue();
    const [showPassword, setShowPassword] = useState(false);
    const [isSignUp, setIsSignUp] = useState(false);

    const [formData, setFormData]=useState(initials);

    const signIn= async()=>{
      
        try{
          await axios.post('/user/signin', formData)
          .then((res)=>{
              if(res.data.message){
                alert(res.data.message);
              }else{
                
                dispatch({
                    type:'SET_USER',
                    user:res.data,
                })
                history.push('/');
              }
          })

        }catch(error){
            console.log(error.message);
        }
    }

    const signUp = async()=>{
        try {
            
            await axios.post('/user/signup', formData)
            .then((res) =>{ 
                if(res.data.message){
                    alert(res.data.message);
                    
                }else{
                    alert(res.data);
                    setIsSignUp(false);
                }
            })
           
        } catch (error) {
            console.log(error.message)
        }
    }

    const handleSubmit= async(e)=>{
        e.preventDefault();
        if(isSignUp){
            signUp();
        }
        else{
            signIn();
        }
    }

    const handleOnChange =(e)=>{
        setFormData({...formData, [e.target.name]: e.target.value});
    }
    
    const handleShowPassword=()=>{
        setShowPassword(!showPassword);
    }

    const switchMode=()=>{  
        setIsSignUp(!isSignUp);
        setShowPassword(false);
    }

    const googleSuccess= async(res)=>{
        const result = res?.profileObj;
        const token = result?.tokenId;

        try {
             dispatch({
                type: 'SET_USER',
                user: {result, token},
            })
            console.log(result);
            history.push('/');
        } catch (error) {
            console.log(error);
        }
    }

    const googleFailure=()=>{
        console.log("Google Sign In was unsuccessful.");
    }

    return (
        <div>
        <Header/>
        <Container component='main' maxWidth="xs">
            <Paper className={classes.paper} elevation={3}>
                <Avatar className={classes.avatar }>
                    <LockOutlinedIcon/>
                </Avatar>
                <Typography variant='h5'>
                    {isSignUp ? 'Sign Up' : 'Sign In'}
                </Typography>
                <form className={classes.form}>
                    <Grid container spacing={2}>
                        {
                            isSignUp && (
                                <>
                                    <CustomInput name="firstName" label="First Name" handleChange={handleOnChange}  half />
                                    <CustomInput name="lastName" label="Second Name" handleChange={handleOnChange}  half />
                                </>
                            )
                        }
                        <CustomInput name="email" label="Email Address" handleChange={handleOnChange} type="email"/>
                        <CustomInput name = "password" label="Password" handleChange={handleOnChange} 
                            showPassword={handleShowPassword} type={showPassword?"text" : "password"}/>
                        {isSignUp && <CustomInput name="confirmPassword" label="Repeat Password" handleChange={handleOnChange} type="password"/>}
                    </Grid>


                    <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit} onClick={handleSubmit}>
                        {isSignUp? "Sign Up" : "Sign In"}
                    </Button>
                    <GoogleLogin
                        clientId="30334865368-69fhmuidebjk26mtpt9rf76uopf1f15f.apps.googleusercontent.com"
                        render={(renderProps)=>(
                            <Button 
                                className={classes.googleButton} 
                                color="primary" 
                                fullWidth 
                                variant="contained"
                                onClick={renderProps.onClick} 
                                disabled={renderProps.disabled} 
                                startIcon={<Icon style={{width:"20px", height:"20px"}}/>}
                            >
                                Google Sign In
                            </Button>
                        )}
                        onSuccess={googleSuccess}
                        onFailure={googleFailure}
                        cookiepolicy='single_host_origin'
                    />
                    <Grid container justify="flex-start" >
                        <Grid>
                            <Button onClick={switchMode} style={{fontSize: "16px"}}>
                                {isSignUp? "Already have an account ? Sign In": "New here? Sign Up"}
                            </Button>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </Container>
        </div>
    )
}

export default Sign
